@media (min-width: 1200px) {
 
  .container{
    max-width:1170px;
  }

  .nav-item.dropdown {
      .dropdown-menu:not(.submenu){
          opacity: 0;
          visibility: hidden;
          display: block;
          top:80%; 
          transform: rotateX(-75deg); 
          transform-origin: 0% 0%;
      }
      &:hover .dropdown-menu:not(.submenu){
          opacity: 1;
          visibility: visible;
          top: 100%;
          transform: rotateX(0deg);
      }

  }

  .nav-item .submenu{ 
    display: none;
    position: absolute;
    left:100%; 
    top:-7px;
    border: 0px;
}
 
    

}
  
  @media (min-width: 992px) and (max-width: 1200px) {

    .site-navigation{
      padding: 0px 20px; 
    }
     

    .carousel-arrow{
      margin-bottom: 30px;;
    }

    .single-course-category{
      margin-bottom: 20px;
    }

    .feature-style-top {
      padding: 40px 20px;
    }

    .testimonials-slides .testimonial-item, .testimonials-slides-3 .testimonial-item {
     padding: 0px;
     margin-bottom: 50px;
    }
    .footer-mid {
      padding-bottom: 0px;
    }

    .cta .cta-inner-section .info-box {
      padding: 70px 52px;
    }
 
  }

  @media (min-width: 768px) and (max-width: 991px) {

    .site-navigation{
      padding: 0px 20px!important; 
    }
   
    .navbar{
      padding:10px 10px;
    }
    .navbar-nav .nav-link{
      padding:14px 20px;
      border-right:0px;
    }

  

    .header-navbar.mobile-menu .primary-menu li .menu-trigger{
      color: #fff;
    }

    //  Testimonial home 1

 
    .testimonials-slides .testimonial-item .testimonial-text, .testimonials-slides-3 .testimonial-item .testimonial-text {
      font-size: 17px;
    }

    .carousel-arrow{
      margin-bottom: 30px;;
    }

    .single-course-category{
      margin-bottom: 20px;
    }

    
    .features:after{
      display: none;
    }
    .features-intro{
      .feature-item{
        margin-bottom: 30px;
      }
    }

    .header-navbar .site-logo {
      width: 25%;
      margin-right: 0px;
    }

    .testimonials-slides .testimonial-item, .testimonials-slides-3 .testimonial-item {
     padding: 0px;
     margin-bottom: 50px;
    }

    .footer-mid {
      padding-bottom: 0px;
    }
    .cta .cta-inner-section .info-box {
      padding: 70px 52px;
    }
   
}


