      line-height: 48px;
    }
    .banner-style-3 .banner-content p {
      max-width: 100%;
    }


    .cta .cta-inner-section .info-box {
      padding: 80px 40px;
    }

    .header-btn {
      margin-left: 0px; 
      border-left: 0px; 
      padding-left: 0px; 
    }

  }


 
  @media(max-width:480px){
